.section-logo {
  background-color: #7f7f7f;

  padding: 9rem 0;

  overflow: hidden;

  @media screen and (max-width: $table-screen) {
    padding: 5rem 0;
  }

  &__title {
    font-size: 4rem;
    font-weight: 700;

    color: white;

    margin-bottom: 6rem;
    margin-top: 0;

    text-align: center;

    @media screen and (max-width: $mobile-screen) {
      font-size: 3rem;
    }

    &--partners {
      margin-top: 9rem;
    }
  }

  &__slide-partners {
    padding: 0 2rem;

    @media screen and (max-width: $table-screen) {
      padding: 0 1rem;
    }

    &-inner {
      display: flex;
      align-items: flex-start;
      justify-content: center;

      position: relative;
    }

    &-icon {
      width: max-content;
      height: 100%;

      position: relative;

      &:hover {
        .img-hover {
          opacity: 1;
        }
      }
    }

    .img-normal {
      height: 7.8rem;
      width: auto;

      display: block;

      @include margin(auto);

      opacity: 1;
      transition: all 0.2s linear;

      position: relative;
      z-index: 10;
    }

    .img-hover {
      height: 7.8rem;
      width: auto;

      @include margin(auto);

      display: block;

      opacity: 0;
      transition: all 0.2s linear;

      position: absolute;
      z-index: 100;
      top: 0;
      left: 0;
      right: 0;

      display: block;
    }
  }

  &__slide-client {
    padding: 0 2rem;

    @media screen and (max-width: $table-screen) {
      padding: 0 1rem;
    }

    &-inner {
      height: 100%;

      display: flex;
      align-items: flex-start;
      justify-content: center;
    }

    &-inner:hover {
      .img-hover {
        opacity: 1;
      }
    }

    &-icon {
      width: 100%;
      height: 100%;

      position: relative;
    }

    .img-normal {
      height: 5.8rem;
      width: auto;
      max-width: 100%;

      @include margin(auto);

      display: block;

      opacity: 1;
      transition: all 0.2s linear;

      position: relative;
      z-index: 10;
    }

    .img-hover {
      height: 5.8rem;
      width: auto;
      max-width: 100%;

      @include margin(auto);

      opacity: 0;
      transition: all 0.2s linear;

      position: absolute;
      z-index: 100;
      top: 0;
      left: 0;
      right: 0;

      display: block;
    }
  }

  .logos {
    display: flex;
    align-items: center;
    justify-content: flex-start;

    width: max-content;
  }

  &__slider-clients{
    animation: animClients 40s linear infinite;
  }

  &__slider-partners{
    animation: animPartners 15s linear infinite;
  }
}

@keyframes animClients {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}

@keyframes animPartners {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(100%);
  }
}